.circles-header {
  margin-bottom: 12px;

  &--table {
    & .navigation {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    & .navigation__container {
      display: flex;
      align-items: center;
      min-height: 40px;
    }

    & .circles-header__editor {
      background: #ffffff;
      box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.12);
      min-width: 41px;
      width: 41px;
      height: 41px;
      border-radius: 50%;
      padding: 0;
    }

    & .add-person {
      border-radius: 50%;
      position: relative;
    }

    & .navigation__item:not(:last-child) {
      margin-right: 15px;
    }

    & .view-list {
      background: #ffffff;
      box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.12);
      border-radius: 4px;
    }
  }

  &--editing {
    & .filters {
      background: #ffffff;
      border: 1px solid rgba(183, 189, 206, 0.3);
      border-radius: 8px;
      outline: none;
      height: 40px;
      box-sizing: border-box;
      font-family: "Proxima Nova";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      /* Black */

      color: $help-text-color;

      &::before {
        width: 19px;
        height: 19px;
        background-image: url("./../../public/img/editor--table.svg");
        background-size: contain;
        top: 11px;
      }
    }

    & .navigation {
      position: relative;
    }

    & .navigation__title {
      display: block;
      margin-left: auto;
      margin-right: 30px;
    }
  }
}

.circles-header__list {
  display: flex;
  align-items: center;
  padding: 7px 11px;
  background: $menu-list-bg;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
}

.circles-header__editor {
  background: #f8f9fc;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  display: block;
  min-width: 170px;
  padding: 11px 4.5px;
  position: relative;
}
