.circles-main {
  width: 100%;
  padding-bottom: 45.5vw;
  position: relative;
  overflow: clip;

  &--table {
    padding: 0;
    background: #ffffff;
    border: 1px solid #f0f4f7;
    box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    overflow-x: scroll;

    & .circles-main__row {
      min-height: 120px;
      display: flex;

      &:not(:first-child):not(:last-child) {
        border-top: 1px solid rgba(183, 189, 206, 0.3);
      }

      &:not(:first-child) {
        .circles-main__ceil:nth-child(2n) {
          background: #f8f8f8;
        }
      }

      &:last-child {
        &::before {
          @include defaul-pseudo-element(100%, 1px);
          background: url("./../../public/img/border-dashed.svg") repeat-x;
        }
      }

      &:first-child {
        min-height: 80px;
      }
    }

    & .circles-main__ceil {
      display: flex;
      flex-grow: 1;
      flex-wrap: wrap;
      padding: 22px 20px;
      gap: 6px 14px;
      width: 260px;
      justify-content: center;
      align-items: center;
      min-width: 170px;

      &:not(:first-child) {
        border-left: 1px solid rgba(183, 189, 206, 0.3);
      }

      &--title {
        width: 142px;
        flex-grow: 0;
        min-width: 142px;
      }
    }

    & .circles-main__user-avatar {
      position: static;
      width: 43%;
      padding: 0;
    }

    & .circles-main__circle {
      font-family: "Source Serif Pro";
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 23px;
    }

    & .circles-main__period {
      font-family: "Source Serif Pro";
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 25px;

      color: #444444;
    }

    & .circle__img {
      position: static;
    }
  }

  &--circle-view-2 {
    background: #ffffff;
    border: 1px solid #f0f4f7;
    box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    padding-top: 18px;
    padding-bottom: 50vw;

    & .circles-main__circles-container {
      width: 100%;
      position: absolute;
    }

    & .periods-menu {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      width: fit-content;
      margin: 0 auto;
    }

    & .periods-menu__button {
      font-family: "Proxima Nova";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: $help-text-color;

      background: #ffffff;
      padding: 11.5px 0;
      min-width: 110px;

      border: 1px solid #b7bdce;
      border-radius: 100px;

      &--active {
        background: #8dbeaf;
        border: 1px solid #326e5b;
        color: #ffffff;
      }
    }

    & .periods-menu__item {
      margin: 0;
    }
  }

  &--editing {
    & .periods-menu__container {
      display: flex;
      align-items: center;
      width: fit-content;
      margin: 0 auto;
    }

    & .periods-menu__edit {
      width: 19px;
      height: 19px;
      margin-left: 14px;
      position: relative;
      transition: $default-menu-transition;

      &::before {
        @include defaul-pseudo-element(100%);
        background: url("./../../public/img/editor--table.svg") no-repeat center
          center;
        background-size: contain;
        @include centering-pseudo-element;
      }
    }

    .periods-menu {
      margin: 0;
    }
  }

  @media (max-width: $mobile-max-width) {
    overflow: visible;
    padding: 40px 15px 34px 15px;
    box-sizing: border-box;
    overflow-x: clip;

    & .circles-main__circles-container {
      position: relative;
      display: flex;
      flex-direction: column;
    }
  }
}

.circles-main__button {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;

  color: #548677;
  border: 1px dashed #000000;
  filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.12));
  border-radius: 4px;
  padding: 11px 15px;
  transition: $default-menu-transition;

  &:hover {
    background-color: #7ee8c8;
  }
}

.circles-main__user-avatar {
  display: block;
  left: 46.35%;
  position: absolute;
  padding-top: 40%;
  width: 10.41%;
  height: auto;
  z-index: 0;

  @media (min-width: 1000px) {
    padding-top: 36%;
  }

  @media (min-width: 1200px) {
    padding-top: 32%;
  }

  @media (min-width: 1400px) {
    padding-top: 28%;
  }

  @media (min-width: 1600px) {
    padding-top: 25%;
  }

  @media (min-width: 2000px) {
    padding-top: 22%;
  }

  @media (max-width: $mobile-max-width) {
    left: 0;
    position: static;
    margin: 0 auto;
    margin-bottom: 65px;
    padding-top: 0;
    width: 28.24%;
    height: auto;
    z-index: 0;
  }
}

@media (max-width: $mobile-max-width) {
  .circles-main__user-avatar-container {
    position: relative;
    display: flex;
    order: -1;
  }

  .circles-main__user-avatar-plus {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 11vw;
    height: 11vw;
    max-width: 50px;
    max-height: 50px;
    min-width: 0;
    top: 17vw;
  }
}

.circles-main__title {
  width: max-content;
}
