.periods-menu__item {
  &:not(:last-child) {
    margin-right: 32px;
  }
}

.periods-menu__button {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  transition: $default-menu-transition;
  box-shadow: none;

  &--active {
    background: #548677;
    border-radius: 100px;
    padding: 5px 8px;
    color: #ffffff;
  }

  &--icon {
    &:hover {
      background: none !important;
      & path {
        fill: $button-text-color !important;
      }
    }
  }
}

.periods-menu__editor {
  align-self: flex-end;
  transition: $default-menu-transition;
}
